table.b-table.b-table-stacked-xl>thead,
    table.b-table.b-table-stacked-xl>tfoot,
    table.b-table.b-table-stacked-xl>tbody>tr.b-table-top-row,
    table.b-table.b-table-stacked-xl>tbody>tr.b-table-bottom-row {
        display: none;
}
    /* inter-row top border */
table.b-table.b-table-stacked-xl>tbody>tr>:first-child {
        border-top-width: .4rem;
}
    /* convert TD/TH contents to "cells". Caveat: child elements become cells! */
table.b-table.b-table-stacked-xl>tbody>tr>[data-label] {
        display: grid;
        grid-template-columns: 40% auto;
        grid-gap: .25rem 1rem;
}
    /* generate row cell "heading" */
table.b-table.b-table-stacked-xl>tbody>tr>[data-label]::before {
        content: attr(data-label);
        display: inline;
        text-align: right;
        overflow-wrap: break-word;
        font-weight: bold;
        font-style: normal;
}
}
/* Details row styling */
table.b-table>tbody>tr.b-table-details>td {
    border-top: none;
}

.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
